<template>
    <div id="detail">
        <h1>BookInfo</h1>
    </div>
</template>

<script>
export default {
    name: 'Books',
    created: function() {
        var id = this.$route.params.id;
        this.$http.get('/api/books' + id)
            .then((response) => {
                this.books = response.data[0]
            })
    },
    data: function() {
        return {
            books: {}
        }
    }
}
</script>